import React from 'react'
import './Circles.css'
const Circles = (props) => {
  return (
    <div className='Circles'>
      {props.circleNo.map((circle, idx) =>
        <img 
        src={props.selectedCircleIdx === idx ? "data:image/png;base64," : "https://i.pinimg.com/originals/26/80/a9/2680a9e44fa2cb4a956f504e0a3fd6be.jpg"}
          height="80"
          key={circle}
          onClick={() => props.handleCirclesSelection(idx)}
        />
      )}
    </div>
  );
}
export default Circles;